import './style.css';

export default function gameplayScreens(src) {
  const cards = document.createElement('div');
  cards.classList.add('screens'); 
  src.forEach((v) => {
    let screen = new Screen(v);
    let picture = screen.createScreen();
    cards.appendChild(picture);
  })
  return cards;
}

class Screen {
  constructor(url) {
    this.url = url;
  }

  createScreen() {
    const card = document.createElement('img');
    card.setAttribute('src', this.url);
    card.classList.add('screen-card');
    return card;
  }
}