import './style.css';
import buttons from '@/components/buttons/buttons-group/creating-buttons'; 
import gameplayScreens from '@/components/screenshots/index'; 
import aboutGame from '@/components/modals/about-game/about';
import Screen_1 from '../../../components/screenshots/img/Screen_1.png';
import Screen_2 from '../../../components/screenshots/img/Screen_2.png';
import Screen_3 from '../../../components/screenshots/img/Screen_3.png';
import Screen_4 from '../../../components/screenshots/img/Screen_4.png';

export default function createMain() {
  const main = document.createElement('main');
  main.classList.add('main');
  const name = document.createElement('h1');
  name.setAttribute('id', 'main');
  name.textContent = 'Fantastic Adventure';

  const menuArea = document.createElement('section');
  menuArea.classList.add('menu');
  const screensArea = document.createElement('section');
  screensArea.classList.add('screenshots');

  const headScreen = document.createElement('h2');
  headScreen.setAttribute('id', 'gameplay');
  headScreen.textContent = 'Gameplay screenshots';

  const btnnames = ['Play', 'Score', 'About'];
  const menu = buttons(btnnames, 'menu-button');
  menu.classList.add('main-menu');

  const aboutG = aboutGame();

  const src = [Screen_1, Screen_2, Screen_3, Screen_4];
  const screens = gameplayScreens(src);

  menuArea.appendChild(name);
  menuArea.appendChild(menu);
  screensArea.appendChild(headScreen);
  screensArea.appendChild(screens);
  
  main.appendChild(aboutG);
  main.appendChild(menuArea);
  main.appendChild(screensArea);

  document.body.appendChild(main);

  document.querySelector('.menu-button').setAttribute('href', 'game.html');

  const aboutBtn = document.querySelector('.menu-button:nth-child(3)');
  function modalAbout() {
    aboutG.classList.remove('hidden');
    aboutG.classList.add('about-game');
  };

  aboutBtn.addEventListener('click', modalAbout);
  aboutBtn.addEventListener('keydown', (e) => {
    if (e.key === 'Enter') {
      modalAbout()
    }
  })
}